  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-color: #ffeecf;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  min-width: 320px;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 13px;
  color: #424242;
}

.app-bar {
  padding: 8px 16px;
  background-color: #ffeecf;
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.app-bar .app-bar__menu {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.app-bar .app-bar__menu button {
  background-color: transparent;
  border: none;
  font-size: 20px;
  padding: 10px;
  cursor: pointer;
  color: #000;
  font-size: 30px;
}

.app-bar .app-bar__brand {
  display: flex;
  align-items: center;
}
.app-bar .app-bar__brand img {
  width: 100px;
}

.app-bar .app-bar__brand h1 {
  color: #000;
  text-transform: uppercase;
  font-size: 22px;
  user-select: none;
}

.app-bar .app-bar__navigation {
  position: absolute;
  top: 70px;
  left: -800px;
  width: 100%;
  transition: all 0.3s;
  padding: 8px;
  background-color: #ffeecf;
  overflow: hidden;
  color: #fff;
}

.app-bar .app-bar__navigation.open {
  left: 0;
}

.app-bar .app-bar__navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: #000;
  padding: 15px;
  margin-bottom: 5px;
  width: 100%;
  font-size: 20px;
}

/* header */

header {
  text-align: center;
}

.nama_logo {
  font-weight: 100;
  font-size: 20px;
  margin-top: 25px;
  display: inline-block;
  color: #212121;
}

.header__menu {
  font-size: 32px;
  padding: 10px 25px;
  margin: 10px auto;
  display: block;
  width: 30px;
}

/* nav */

.nav__list {
  width: 100%;
  padding: 0;
  margin: 0;
  text-align: center;
}

.nav__item {
  box-sizing: border-box;
  display: inline-block;
}

nav a {
  display: inline-block;
  padding: 1.3em;
  text-decoration: none;
  color: #616161;
}

nav a:hover {
  text-decoration: double;
  color: rgb(20, 9, 9);
}

/* Jumbotron */
.jumbotron {
  height: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rosybrown;
  color: aliceblue;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-repeat: no-repeat;
  background-size: cover;
}

.jumbotron .jumbotron-inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.jumbotron .jumbtron-inner h2 {
  font-size: 23px;
  font-weight: 700;
  background-color: #7d4a34;
}

.jumbotron .jumbotron-inner p {
  font-size: 17px;
  text-align: center;
  padding: 20px;
}

.jumbotron .jumbotron-inner .jumbotron-btn {
  display: flex;
  justify-content: center;
}

.jumbotron .jumbotron-inner .jumbotron-btn .btn1 {
  padding: 18px 22px;
  border-radius: 5px;
  background-color: #fff;
  color: rgb(75, 60, 60);
  margin-top: 14px;
}

.headline .headline-context img {
  width: 100%;
  /* aspect-ratio: 16 / 9; */
}

.headline .headline-context {
  color: #000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #ffeecf;
  padding: 0 200px;
  /* margin: 200px; */
  border-radius: 20px;
}

.headline__figure {
  width: 100%;
}

.headline__figure figcaption {
  text-align: center;
  margin-bottom: 15px;
}

.headline__content-title {
  color: #7d4a34;
  font-size: 1.5em;
  letter-spacing: 1px;
  margin-bottom: 10px;
}

.headline__content-description {
  font-size: 1em;
  letter-spacing: 1px;
  word-spacing: 3px;
  line-height: 1.5em;
}

.headline__content-button {
  display: inline-block;
  margin-top: 10px;
  border-radius: 10px;
  text-decoration: none;
  line-height: 44px;
  padding: 0 0.8em;
  min-width: 44px;
  min-height: 44px;
  background-color: #7a4247;
  color: cornsilk;
}

.resto-item__header__poster {
  aspect-ratio: 3 / 4;
  width: 100%;
  object-fit: cover;
  object-position: center;
  overflow: hidden;
  overflow-clip-margin: content-box;
}

.resto-item__rating-location {
  margin-top: 10px;
  display: flex;
  gap: 10px;
  align-items: flex-start;
  padding-right: 20px;
}

.resto-item__content h3 {
  margin-top: 10px;
  text-align: center;
}
.resto-item__content h3 a {
  padding: 20px;
  font-size: 20px;
  color: #7a4247;
}

a {
  text-decoration: none;
}

.restaurants {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

/* card yt*/

.content-section .card {
  flex: 3;
  margin: 20px 20px;
  box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  padding: 5px;
}

.content-section .card img {
  width: 100%;
  aspect-ratio: 16/9;
}

.content-section .card h2 {
  margin: 15px 0px;
  font-size: 28px;
  color: #1662a1;
}

.content-section .card p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
  font-size: 16px;
  line-height: 1.5;
  padding: 0px 10px;
  margin-bottom: 20px;
  text-align: justify;
}

.resto-city {
  font-weight: thin;
  margin: 0;
  background-color: #7a4247;
  color: #fff;
  border-radius: 5px;
  margin-left: 10px;
}

.resto-rating {
  margin: 0;
  background-color: #7a4247;
  color: #fff;
  border-radius: 5px;
}

/* detail info*/

.info-container {
  display: flex;
  gap: 20px;
}

.detail-information {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin: 30px;
}
.detail-information .restaurant__poster {
  border-radius: 10px;
  width: 400px;
  height: 300px;
}

.information {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.comment-container {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 20px;
}

.comment-review {
  width: 250px;
  border: 1px solid #000;
  padding: 10px;
  border-radius: 10px;
}

/* Styling untuk footer */
.footer {
  background-color: #7a4247;
  color: #fff8dc;
  padding: 20px 0;
  text-align: center;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
}

.footer p {
  margin: 0;
}

.footer .footer-content .footer__teks a {
  color: #fff8dc;
}

.footer__teks a {
  min-width: 44px;
  min-height: 44px;
  padding-block: 1rem;
}

.skip-link {
  position: absolute;
  top: -80px;
  left: 0;
  background-color: var(--btn);
  color: white;
  padding: 8px;
  z-index: 10;
}

.skip-link:focus {
  top: 100px;
}

.like {
  font-size: 18px;
  position: fixed;
  bottom: 16px;
  right: 16px;
  background-color: #7a4247;
  color: white;
  border: 0;
  border-radius: 50%;
  width: 55px;
  height: 55px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}
