___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (max-width: 1250px) {
  .restaurants {
    grid-template-columns: 1fr 1fr;
  }

  .headline .headline-context {
    padding: 0 100px;
  }
}
@media screen and (max-width: 900px) {
  .info-container {
    flex-direction: column;
    align-items: center;
  }

  .headline .headline-context {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
  }

  .desc-item {
    text-align: justify;
  }

  .food-menu {
    text-align: center;
  }
  .drink-menu {
    text-align: center;
  }

  .review {
    text-align: center;
  }

  .comment-container {
    justify-content: center;
  }
}

@media screen and (max-width: 499px) {
  nav {
    z-index: 10;
    background-color: #fff;
    width: 300px;
    position: absolute;
    -webkit-transform: translate(-300px, 0);
    transform: translate(-300px, 0);
    transition: transform 0.3s ease;
  }

  .open {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }

  .nav__item {
    display: list-item;
    border-bottom: 1px solid #e0e0e0;
    width: 100%;
    text-align: left;
  }
}

@media screen and (max-width: 420px) {
  .detail-information .restaurant__poster {
    border-radius: 10px;
    width: 300px;
    height: 200px;
  }
}

@media screen and (min-width: 455px) {
  .headline__content {
    padding: 16px 16px;
  }

  .nama_logo {
    font-size: 28px;
  }

  .headline__description {
    font-size: 16px;
  }
}

@media screen and (min-width: 500px) {
  .header__menu {
    display: none;
  }
}

@media screen and (max-width: 768px) {
  .restaurants {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
  }
  .jumbotron{
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  }
 
}

@media screen and (min-width: 660px) {
  .app-bar {
    grid-template-columns: 1fr auto;
    padding: 4px 20px;
  }

  .app-bar .app-bar__brand h1 {
    font-size: 2em;
  }

  .app-bar .app-bar__menu {
    display: none;
  }

  .app-bar .app-bar__navigation {
    position: static;
    width: 100%;
    padding-top: 20px;
  }

  .app-bar .app-bar__navigation ul li {
    display: inline-block;
  }

  .app-bar .app-bar__navigation ul li a {
    display: inline-block;
    width: 120px;
    text-align: center;
    margin: 0;
    font-size: 20px;
    padding: 12px;
  }

  .app-bar .app-bar__navigation nav ul li a:hover {
    color: #7a4247;
  }
}
